.main {
  width: 80%;
  height: 915px;
  padding: 55px 55px 46px;
}

.main__banner {
  width: 100%;
  height: 187px;
  border-radius: 20px;
  /* border: 2px solid red; */
}

.main__banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.main__title {
  margin: 48px 0;
  font-family: "Roboto";
  font-weight: 600;
  font-size: 23px;
  line-height: 16px;
  letter-spacing: 0.4px;

  color: #ffffff;
}
.main__wrapper {

  display: grid;
  grid-template-columns: 430px 430px;
  gap: 54px 30px;

  width: 100%;
}

.main__card {
  /* width: 430px; */
  overflow-y: hidden;
  height: 230px;
  border-radius: 20px;
  position: relative;
}
.main__card:hover .card__info{
    transition: 500ms;
    bottom: 0;
}

.card__info {
  position: absolute;
  bottom: -70px;
  width: 100%;
  height: 70px;
  backdrop-filter: blur(4px);
  background-color: rgba(60, 56, 64, 0.89);
  border-radius: 10px 10px 20px 20px;
  padding: 17px 20px;
  display: flex;
}
.card__duration {
    margin-top: 5px;
  width: 80px;
  height: 25px;
  margin-left: auto;
  background-color: #885ffb;
  border-radius: 20px;

  font-weight: 400;
  font-size: 16px;
  line-height: 16px;

  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.4px;

  color: #ffffff;
}

.card__title {
  margin-left: 15px;
}
.card__title p {
  font-family: "Sarabun";
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;

  letter-spacing: 0.4px;

  color: #ffffff;
}
.card__title span {
  font-family: "Sarabun";
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;

 
  letter-spacing: 0.4px;

  color: rgba(255, 255, 255, 0.5);
}
.card__info img {
  width: 35px;
  height: 35px;
}
.card__info img:hover {
    cursor: pointer;
}
.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}


@media(max-width:1440px) {
  .main__wrapper {
    grid-template-columns: 400px 400px;
  }
}
@media(max-width:1380px) {
  .main__wrapper {
    grid-template-columns: 350px 350px;
  }
}
@media(max-width:1280px) {
  .main__wrapper {
    grid-template-columns: 300px 300px;
  }
}
@media(max-width:1180px) {
  .main__wrapper {
    grid-template-columns: 250px 250px;
  }
  .main {
    padding: 55px 30px 46px;
  }
}
@media(max-width:930px) {
  .main__wrapper {
    grid-template-columns: 230px 230px;
  }
  .main {
    padding: 55px 20px 46px;
  }
}
@media(max-width:800px) {
  .main__wrapper {
    grid-template-columns: 400px ;
    align-items: center;
    justify-content: center;
  }
  .main {
    padding: 55px 0px 46px;
  }
  .main__title {
    text-align: center;
  }
}
