.nav {
  width: 100%;
  border: 1px solid #cccccc;
}

.nav__body {
  padding: 47px 0;
  display: flex;
  align-items: center;
}

.nav__logo {
  font-family: "Prompt";
  font-weight: 700;
  font-size: 23px;
  line-height: 16px;
  letter-spacing: 0.5px;
  color: #ffffff;
}
.nav__list {
  margin-left: 189px;
  width: 301px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__link {
  font-family: "Roboto";
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.5px;
  color: #ffffff;
}
.nav__link:hover {
    color: #cccccc99;
    transition: 300ms;
}
.nav__icons {
  margin-left: auto;
  width: 148.5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__icons img:hover {
    cursor: pointer;
    transform: scale(1.1);
}
.nav__icons img:active {
    transform: scale(1);
}


/* media */
 @media(max-width: 800px) {
   .nav__body {
     flex-direction: column;
     align-items: center;
     justify-content: space-between;
   }

   .nav__icons  {
     margin: 0;
   }
   .nav__list {
        margin: 25px 0;
   }
 }