.comments {
  width: 440px;
  height: 915px;
  border: 1px solid #cccccc;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.comments__wrapper {
  height: 520px;
  overflow-y: scroll;
}

.comments__title {
  padding: 30px;
  border-bottom: 1px solid #cccccc;
}
.comments__title h2 {
  font-family: "Prompt";
  font-style: normal;
  font-weight: 700;
  font-size: 23px;
  line-height: 16px;
  letter-spacing: 0.5px;

  color: #ffffff;
}
.comments__body {
  padding: 40px 40px 0 40px;
}
.comments__body h4 {
  font-family: "Prompt";
  font-weight: 500;
  font-size: 18px;
  line-height: 16px;
  margin-bottom: 10px;
  letter-spacing: 0.5px;

  color: #ffffff;
}
.comments__body p {
  font-family: "Prompt";
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.5px;
  color: #ffffff;
}
.form {
  padding: 40px;
  margin-top: 40px;
  border-top: 1px solid #cccccc;
}

.form__username {
  width: 360px;
  height: 40px;
  border: 2px solid #ffffff;
  border-radius: 5px;
  padding-left: 15px;
  background-color: transparent;
  color: #ffffff;
  margin-bottom: 15px;
}
.form__username::placeholder {
  font-family: "Prompt";
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.5px;
  color: #ffffff;
}
.form__comment {
  width: 360px;
  height: 120px;
  background-color: transparent;
  border: 2px solid #ffffff;
  border-radius: 5px;
  padding-left: 15px;
  padding-top: 24px;
  color: #ffffff;
}
.form__comment::placeholder {
  font-family: "Prompt";
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.5px;
  color: #ffffff;
}
.form button {
  width: 80px;
  height: 30px;
  background-color: #885ffb;
  border-radius: 5px;
  margin-top: 15px;
  border: none;

  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  display: block;
  margin-left: auto;
  letter-spacing: 0.4px;

  color: #ffffff;
}
.form button:hover {
  cursor: pointer;
}

@media (max-width: 1280px) {
  .form__username {
    width: 250px;
  }
  .form__comment {
    width: 250px;
    height: 56px;
    padding-top: 10px;
  }
}

@media (max-width: 1100px) {
  .comments {
    width: 400px;
    padding-bottom: 50px;
  }
}
@media (max-width: 930px) {
  .comments {
    width: 350px;
    padding-bottom: 50px;
  }
  .form__comment {
    width: 250px;
    height: 36px;
  }
}
@media (max-width: 868px) {
  .comments {
    /* width: 300px; */
    height: 1010px;
  }
  .form__comment {
    width: 200px;
    height: 36px;
  }

  .form__username {
    width: 200px;
  }

  .form {
    padding: 20px;
    margin-top: 20px;
  }
}

@media (max-width: 868px) {
  .comments {
    margin-top: 600px;
    height: 860px;
    width: 390px;
  }
  .form__comment {
    width: 310px;
    height: 36px;
  }

  .form__username {
    width: 310px;
  }
  .form {
    padding: 40px ;
  }
}
